import React from 'react';
import Box from '@material-ui/core/Box';

export function Policy() {
    return (
        <Box display="flex" flexDirection="column">
            <h2>sinkのデータ利用について</h2>
            <p>
                sinkでタスクとして入力したデータはすべてブラウザに保存され，外部に送信されることはありません。
            </p>
            <h2>Google Analyticsについて</h2>
            <p>
                sinkでは利用状況を把握するためにGoogle Analyticsを利用しています。
            </p>
            <p>
                Google Analytics から提供されるCookie を使用していますが、Google Analytics によって個人を特定する情報は取得していません。
            </p>
            <p>
                Google Analytics の利用により収集されたデータは、Google社のプライバシーポリシーに基づいて管理されています。Google Analyticsの利用規約・プライバシーポリシーについてはGoogle Analytics のホームページでご確認ください。
                <ul>
                    <li><a href="https://marketingplatform.google.com/about/analytics/terms/jp/">Google アナリティクス サービス利用規約</a></li>
                    <li><a href="https://policies.google.com/">Google ポリシーと規約</a></li>
                </ul>
                なお、Google Analyticsのサービス利用による損害については、当サイトは責任を負わないものとします。
            </p>
            <p>
                また、<a href="https://tools.google.com/dlpage/gaoptout?hl=ja">Google アナリティクス オプトアウト アドオン</a>を使用することで，Google アナリティクス JavaScript（analytics.js、gtag.js）によるデータの使用を無効にできます。
            </p>
        </Box>
    );
}
